import WHeader from '../modules/wheader/wheader.vue'
// import popAlert from '../modules/pop/pop-alert.vue'
export default {
  name: 'orderList',
  data() {
    return {
      defaultImg: 'this.src="' + require('../../../static/img/pic.png') + '"',
      headerTitle: "我的任务",
      // taskInfo: [{ taskType: 1 }],
      img: '',//头像图片
      mescroll: null,
      dataList: [],
      message: {
        userid: '',
        page: '-1',
        pageSize: '5'
      }
    }
  },
  components: {
    WHeader
    // popAlert
  },
  mounted() {
    var userinfo = this.getLocation("userInfo");
    console.log(userinfo);
    this.message.userid = userinfo.id;

    this.mescroll = new MeScroll("mescroll", {
      down: {
        auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
        callback: this.downCallback //下拉刷新的回调
      },
      up: {
        auto: true, //是否在初始化时以上拉加载的方式自动加载第一页数据; 默认false
        callback: this.upCallback //上拉回调,此处可简写; 相当于 callback: function (page) { upCallback(page); }
      }
    });
  },
  methods: {
    downCallback() {
      var API = this.$store.state.api;
      this.message.page = 0;

      this.$http.post(API.taskLoadPage, this.message, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
        emulateJSON: true
      })
        .then(function(res) {
          var result = res.body;
          if (result.state == 1) {
            this.dataList = result.data;
            //联网成功的回调,隐藏下拉刷新的状态
            this.mescroll.endSuccess(result.data.length);
          } else {
            this.showToast(result.message, 2, 1500);
            //联网失败的回调,隐藏下拉刷新的状态
            this.mescroll.endErr();
          }
        });
    },
    upCallback() {
      var API = this.$store.state.api;
      this.message.page++;
      var self = this;

      this.$http.post(API.taskLoadPage, this.message, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
        emulateJSON: true
      })
        .then(function(res) {
          var result = res.body;
          console.log(result);
          if (result.state == 1) {
            this.dataList = this.dataList.concat(result.data);
            //联网成功的回调,隐藏下拉刷新的状态
            this.mescroll.endSuccess(result.data.length);
          } else {
            this.showToast(result.message, 2, 1500);
            //联网失败的回调,隐藏下拉刷新的状态
            this.mescroll.endErr();
          }
        });
    },
    back(callback,time) {
      var self = this;
      if(typeof callback === 'number'){
        time = callback;
        callback = undefined;
      }
      var timer = setTimeout(function() {
        if(callback){
          callback.apply(self);
        }else{
          self.$router.back();
        }
        clearTimeout(timer);
      }, time || 0);
    },
    updateState(index) {
      console.log(index);
      var self = this;
      var API = this.$store.state.api;
      var data = this.dataList[index];
      if(type == 2)return;
      this.showPop('任务', '确认打扫完成', '确认', function() {

        self.$http.post(API.updateTask, {
          id:data.task.id,
          userid:data.task.userid,
          houseid:data.task.houseid
        }, {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
          emulateJSON: true
        })
          .then(function(res) {
            var result = res.body;
            self.closePop();
            if (result.state == 1) {
                // self.dataList.splice(index,1);
                self.showToast(result.message, 1, 1500);
            } else {
              self.showToast(result.message, 2, 1500);
            }
          });
      });
      

    }
  }
}



