var agreement = {
    html: "<p>租房合同</p><p>为更好的保障甲方房东，乙方租客双方的权益，特订立此合同，双方应依法遵守。</p><p>一、	所租房屋用途</p><p>1、	乙方向甲方承诺，租赁房屋仅作为住宿使用，并遵守国家相关法律法规。</p><p>2、	在租赁期内，未事先征得甲方同意，乙方不得擅自改变该房屋的用途。</p><p>3、	未获得甲方许可不得将房屋转租，转借他人使用，如因乙方擅自转租，转借他人使用所造成的后果全部由乙方承担。</p><p>二、	房屋租用期限</p><p>1、	甲方于乙方预定入住当日14:00前向乙方交付该房屋，租赁期乙方预定入住当日 14:00起至 乙方预定承租期结束日12:00止。</p><p>2、	乙方于当日14:00之前下单，甲方需在14:00之前向甲方交付房屋，如遇甲方有情况延误交付时间至多3小时，超过时限甲方向乙方全额退款。</p><p>3、	甲方于当日14:00之后下单，乙方则需在下单时间起3小时内向乙方交付房屋，如无法交付则甲方有权要求全额退款。</p><p>4、	租赁期满后，乙方应按时归还房屋。乙方如需续租，则必须在租赁期满3小时前向甲方提交续租申请，并下单续约成功后方可继续使用该房屋。</p><p>5、	租赁期满后，若乙方未能按时归还房屋，超过租赁期限1小时（未满1小时按1小时计算）甲方有权向甲方收取当日房租一半的费用。超过租赁期限2小时(未满2小时按2小时计算)甲方有权向甲方收取当日房租全额费用。</p><p>三、	租金及支付方式</p><p>1、	该房屋今日租赁的日租金为人民币{{pledge}}元整。</p><p>2、	乙方支付租金方式：一次性支付</p><p>四、	保证金</p><p>1、	为确保房屋及所属设施的安全与完好，租赁期内相关费用能如期结算，甲乙双方同意本协议的保证金为人民币{{cost}}元整</p><p>2、	乙方应于本协议订时付清全额保证金。</p><p>3、	租赁期满后，乙方清点，交换房屋及设施，并付清相关费用，甲方须在清点设施，相关费用支付情况后，无问题的情况下24小时内向乙方退还保证金。</p><p>4、	甲方在清点设施，检查相关费用支付情况，如乙方有所遗漏，费用可从保证金内扣除。</p><p>五、	责任义务</p><p>1、	乙方必须向甲方提供有效的居民身份证和联系方式，按约定的项目如实登记。</p><p>2、	需租用3个月以上的，同时向当地派出所申报办理居住登记证。</p><p>3、	乙方在租赁期内保证在该租赁房屋内的所有活动均合乎国家的法律及该承租地物业管理相关规定，不得有任何违法行为。否则视为违约，相关责任与甲方无关。</p><p>4、	乙方租赁期内在该房屋造成的任何人身意外伤害，或导致伤残死亡的，甲方没有赔偿责任义务，相关责任与甲方无关。</p><p>5、	乙方应按协议的约定，按时支付租金及其他各项费用，否则视为违约。</p><p>6、	乙方会客必须遵守公安机关的规定，未经许可，不得擅自或强行将会客人员带入承租房。</p><p>7、	乙方严禁将易燃、易爆、剧毒、腐蚀性和放射性等危险品带入承租房；严禁卖淫、嫖宿、赌博、斗殴。</p><p>8、	乙方禁止在承租房私自使用电热器具；禁止在承租房私自拉结或安装电器设备；禁止在非厨房区域煮食。</p><p>9、	乙方要文明礼貌，不得酗酒滋事、高声喧哗、高声音响，影响他人休息。</p><p>10、	乙方须协助甲方做好安全防范工作，发现违法犯罪人员或可疑情况，应及时报告公安机关。</p><p>11、	遇民警检查时，乙方应主动出示证件，说明情况，予以协助。</p><p>12、	未经甲方同意，乙方不能对租赁房屋进行装修、改造或超出甲方书面同意的范围装修或增设附属设施，甲方可要求乙方恢复原状或赔偿损失。</p><p>13、	未经甲方同意，乙方不得将该房屋转租，并爱护使用该房屋及设施，如因乙方的过失或错使房屋及设施受到损坏，乙方应承担全部责任，甲方可要求乙方按照房屋内设施清单（附件本协议后）照价赔偿。&nbsp;</p><p>六、	协议终止及解除条件</p><p>&nbsp;1、租赁期内甲乙双方在无违约的前提下均不得单方提前终止本协议。</p><p>七、	其他条款</p><p>1、	甲乙双方在签订本协议时，必须具有完全民事行为能力，对各自的权利，义务、责任清楚明白，并愿按本协议规定严格执行。</p><p>2、	本合同双方应依法遵守，如有异议，甲乙双方应协商解决，协商不成，应向乙方所在地人民法院提起诉讼。</p><p>3、	本协议为电子文档，具有纸质协议同等效力。阅读并勾选后生效。</p><p>出租方（甲方）：{{AName}}</p><p>身份证号码：{{ACardId}}</p><p>电话：{{APhone}}</p><p>签约日期：以乙方下单时间为准</p><p>承租方（乙方）：{{BName}}</p><p>身份证号码：{{BCardId}}</p><p>电话：{{BPhone}}</p><p>签约时间：以乙方下单时间为准</p>"
};

module.exports = agreement;


