import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  export default {
   props: {
     images: Array
   },
    data() {
      return {
        listImg:this.images
      }
    },
    mounted() {
//      console.log('mounted', this)
      var swiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        paginationClickable: true,
        loop: true,
        speed: 600,
        autoplay: 4000,
        onTouchEnd: function() {
          swiper.startAutoplay()
        }
      });
    }
  }



