export default {
    name: 'indexFooter',
    data() {
        return {
           selected: this.$store.state.footerSelect
        }
    },
  methods:{
      toUser(){
        this.$router.push('user');
      },
      toOrderList(){
        //初始化订单项索引值
        this.removeLocation('orderListItemIndex');
        this.$router.push('orderList');
      },
      toIndex(){
        this.$router.push('/')
      }
  }
}



