  import WHeader from '../wheader/wheader.vue'
    export default {
        name: 'selectOne',
        data() {
            return {
                provList: provs_data,
              headerTitle: "选择地区"
            }
        },
      components:{
        WHeader
      },
      methods:{
        getCity(id,name){
          //初始化城市信息
          var cope = {
            provinceName: "",
            provinceId: "",
            cityName:"",
            cityId: "",
            countyName:"",
            countyId: ""
          };
          cope.provinceName = name;
          cope.provinceId = id;
          this.$store.state.threeArea = cope;
//          console.log(cope);
          //判断该省是否有其他市，否则直接返回到首页
          if(citys_data[id]){
            this.$router.push("/city");
          }else{
            //接下来获取城市数据
            this.$router.push("/");
          }

        }
      }
    }



